import { utilActions } from '../constants/utilActionTypes'

const initialState = {
    isLoggedIn: false,
    user: {},
    membership: {}
}

export const utilReducer = (state = initialState, action) => {
    switch (action.type) {
        case utilActions.CHANGE_LOGIN_STATUS:
            return {...state, isLoggedIn: action.payload}
        case utilActions.SET_USER:
            return {...state, user: action.payload}
        case utilActions.SET_MEMBERSHIP:
            return {...state, membership: action.payload}
        default:
            return state
    }
}

