import { utilActions } from '../constants/utilActionTypes'

export const changeLoginStatus = (status) => {
    return {
        type: utilActions.CHANGE_LOGIN_STATUS,
        info: "Change login status",
        payload: status
    }
}

export const setUser = (user) => {
    return {
        type: utilActions.SET_USER,
        info: "Set logged in user",
        payload: user
    }
}

export const setMembership = (membership) => {
    return {
        type: utilActions.SET_MEMBERSHIP,
        info: "Set logged in user's membership",
        payload: membership
    }
}